h1 {
  /* ${props => props.theme.fontLeckerli} */
  /* ${props => props.theme.fontAmatic} */
  // color: ${props => props.theme.darkNavy};
  text-align: center;
  font-size: 40px;
  /* font-size: 64px; */
  font-weight: 1000;
  line-height: 1.4;
  @media (max-width: 767px) {
    font-size: 30px;
  }
}
.itemsBox {
  padding: 20px;
  /* background: rgba(0, 0, 0, 0.5); */
  @media (max-width: 767px) {
    padding: 10px;
  }
  .itemBoxGrid {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;

    @media (max-width: 767px) {
      grid-gap: 10px;
      grid-template-columns: 1fr;
    }

    /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */

    .itemBox {
      /* background: rgba(255, 0, 0, 0.5); */
      display: flex;
      justify-content: center;
      .imageBox {
        overflow: hidden;
        width: 160px;
        height: 160px;
        border-radius: 80px;
        @media (max-width: 1023px) {
          width: 140px;
          height: 140px;
          border-radius: 70px;
        }
        /* width: 200px;
        height: 140px;
        border-radius: 8px; */
        /* position: relative; */
        img {
          /* position: absolute;
          left: 50%;
          transform: translateX(-50%); */
          /* -webkit-transform: translateZ(0); */
          /* -webkit-perspective: 1000;
          -webkit-backface-visibility: hidden; */
          width: 160px;
          height: 160px;
          border-radius: 80px;
          perspective: 1000;
          backface-visibility: hidden;
          user-select: none;
          @media (max-width: 767px) {
            width: 140px;
            height: 140px;
            border-radius: 70px;
          }
        }
      }
      .contentBox {
        // flex: 1;
        // flex-grow: 1;
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media (max-width: 767px) {
          margin-left: 10px;
        }
        .name {
          // ${props => props.theme.fontPlayfair}
          font-weight: bold;
          font-size: 18px;
        }
        .price {
          font-size: 18px;
          font-weight: bold;
          // ${props => props.theme.fontPlayfair}
          // color: ${props => props.theme.darkRed};
        }
        .description {
          line-height: 1;
        }
      }
    }
  }
}
